import React, {useState, useEffect} from 'react';
import ArticleList from '../component/ArticleList';
import CommentsList from '../component/CommentsList';
import UpvotesSection from '../component/UpvotesSection';
import AddCommentForm from '../component/AddCommentForm';
import NotFoundPage from './NotFoundPage';
import articlesContent from './article-conten';

const ArticlePage = ({match}) => {
    const name = match.params.name
    const article = articlesContent.find(article => article.name === name)

    const [articleInfo, setArticleInfo] = useState({ upvotes : 0, comments : []})

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`)
            const body = await result.json()            
            setArticleInfo(body)
        }
        fetchData()
    }, [name])

    if (!article) return <NotFoundPage />

    const otherArticles = articlesContent.filter(article => article.name !== name)

    return (
        <>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name}
                upvotes={articleInfo.upvotes}
                setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm articleName={name} 
                setArticleInfo={setArticleInfo} />
            <h3>Other Articles:</h3>
            <ArticleList article={otherArticles} />
        </>
    )
}
export default ArticlePage;